import Image from "next/image"
import { useState } from "react";
import { runPythonScript } from "./api";

export default function App(){
  const [option, setOption] = useState("1");
  const [value, setValue] = useState("");
  const [output, setOutput] = useState("");
  const [loading, setLoading] = useState(false);

  async function handleSubmit(e: React.FormEvent) {
    e.preventDefault();
    setLoading(true);
    setOutput("");
    setTimeout(async () => {
      const res = await runPythonScript(option, value);
      setOutput(res.output || res.error || "No output");
      setLoading(false);
    }, 5500);
  }

  return <div className="">
    <Image alt="logo" src="/thapar.jpeg" width={32} height={32} className="w-15 ml-175 mt-25" />
    <form onSubmit={handleSubmit} className="mt-8">
      <label>
        Option:
        <select value={option} onChange={e => setOption(e.target.value)}>
          <option value="1">Course Code</option>
          <option value="2">Course Name</option>
        </select>
      </label>
      <input
        type="text"
        placeholder={option === "1" ? "Enter Course Code" : "Enter Course Name"}
        value={value}
        onChange={e => setValue(e.target.value)}
        className="ml-2"
      />
      <button type="submit" className="ml-2">Submit</button>
    </form>
    {loading && <div className="mt-4">Processing... Please wait.</div>}
    {output && <div className="mt-4"><pre>{output}</pre></div>}
  </div>
}